import { Injectable, Output } from "@angular/core";

@Injectable({
  providedIn: "root"
})
export class ShowInitialService {
  show: boolean = true;
  numbers: number;
  wordsFetch = [];

  constructor() {
    fetch(
      "https://raw.githubusercontent.com/felipejn/jogodaforca/master/palavras.txt"
    )
      .then(res => {
        // console.log(res.clone().text());
        return res.clone().text();
      })
      .then(data => {
        let arrWords = data.split("\n");
        console.log(arrWords);
        this.wordsFetch = arrWords;
      });
  }

  getWordsFetch() {
    return this.wordsFetch;
  }

  getNumber() {
    return this.numbers;
  }
  setNumber(num) {
    this.numbers = num;
  }

  getShow() {
    return this.show;
  }

  setShow() {
    this.show = !this.show;
  }
}
